<template>
  <div class="preferences-wrap">
    <div class="heading-text">
      <h4>Looking for a certain category of prize?</h4>
    </div>
    <div class="selection-wrap">
      <ul class="selection-grid">
        <li v-for="category in categories" :key="category" v-on:click.capture="setPreference" class="item">
          {{ category }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "Preferences",
  props: {},
  data() {
    return {
      categories: ["Sports", "Gaming", "Animals", "Childrens", "Novelty", "Random"]
    };
  },
  methods: {
    setPreference: function(event){
      const e = event.target.innerHTML;
      let p = e.trim();
      localStorage.setItem("preference", p);
      this.proceed();
    },
    proceed: function(){
      this.$router.push("Purchase");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import url("https://fonts.googleapis.com/css2?family=Sen&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

@mixin main-font {
  font-family: "Sen", sans-serif;
}
@mixin secondary-font {
  font-family: "Roboto", sans-serif;
}

.preferences-wrap {
  width: 100vw;

  .heading-text {
    @include main-font;
    color: white;
    padding: 5%;
    text-align: center;
  }
}

.selection-grid{
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  justify-items: center;
  padding: 2% 0;

  .item {
    @include main-font;
    list-style: none;
    width: 80%;
    margin: 4% auto;
    padding: 2.5% 0;
    text-align: center;
    height: 7vh;
    background-color: aliceblue;
    color: #308fdb;
    border: 2px solid #308fdb;
  }
}
@media only screen and (min-width: 768px) {
  p {
    font-size: 1.5rem;
  }
  h4 {
    font-size: 2rem;
  }
  .preferences-wrap .heading-text {
    padding: 5% 5% 0 5%;
  }
}

@media only screen and (min-width: 992px) {
  .selection-grid {
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    justify-items: center;
    flex-wrap: wrap;

    .item {
      @include main-font;
      list-style: none;
      max-width: 27%;
      margin: 4% 2%;
      text-align: center;
      background-color: aliceblue;
      color: #308fdb;
      border: 2px solid #308fdb;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1.5rem;
      cursor: pointer;
    }
  }
}
</style>
