<template>
  <div class="flex-parent">
    <div class="summary-wrapper">
      <p class="order-head">Your Order:</p>
      <div class="orders-flex">
        <div class="orders-charity">
          <p class="headings">Chosen Charity:</p>
          <p>{{ charity }}</p>
        </div>
        <div class="orders-cost">
          <p class="headings">Price:</p>
          <p>{{ cost }}</p>
        </div>
        <div class="orders-quantity">
          <p class="headings">Quantity:</p>
          <p>{{ quantity }}</p>
        </div>
      </div>
      <h3 class="total text-right">Total: {{ totalCost }}</h3>
    </div>
    <div class="payment-wrapper">
      <p class="payment-head text-center">Payment:</p>
      <p class="icon-head text-center">Accepted Cards:</p>
      <div class="icon-wrapper">
        <img src="../assets/paypal.png" alt="paypal" />
        <img src="../assets/visa.png" alt="visa" />
        <img src="../assets/mastercard.png" alt="mastercard" />
      </div>
      <div class="card-form">
        <label>Cardholder Name:</label>
        <input type="text" placeholder="John Smith" v-model="cardHolder"/>
        <label>Card Number:</label>
        <input type="text" placeholder="1234-2345-3456-4567" v-model="cardNumber"/>
        <div class="split-fields">
          <div class="half">
            <label>Expiry Month:</label>
            <input class="half-item" type="text" placeholder="March" v-model="expiryM" />
          </div>
          <div class="half">
            <label>Expiry Year:</label>
            <input class="half-item" type="text"  placeholder="2020" v-model="expiryY" />
          </div>
        </div>
        <label>CVV (Security Code):</label>
        <input type="text" id="input5" placeholder="987" v-model="cvv"/>
        <p class="text-center py-2 error" v-if="paymentFailed">Error: Please make sure all fields are filled out and try again.</p>
        <button v-on:click="confirmation()">Pay Now</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Summary",
  props: {},
  data() {
    return {
      charity: "",
      quantity: "",
      cost: "",
      totalCost: "",

      cardHolder: "",
      cardNumber: "",
      expiryM: "",
      expiryY: "",
      cvv: "",
      paymentFailed: false,

    };
  },
  methods: {
    getData: function() {
      let q = localStorage.getItem("quantity");
      let c = localStorage.getItem("charity");

      this.charity = c;
      this.quantity = q;
      console.log(this.charity);
    },
    getCost: function() {
      if (this.quantity === "1") {
        this.cost = "£10";
        this.totalCost = "£10";
      }
      if (this.quantity === "2") {
        this.cost = "£7.50";
        this.totalCost = "£15";
      }
    },
    confirmation: function() {
      if(this.cardHolder === "" || this.cardNumber === "" || this.expiryM === "" || this.expiryY === "" || this.cvv === ""){
        this.paymentFailed = true;
      } else {
        this.paymentFailed = false;
        this.$router.push("receipt");
      }
    }
  },
  created() {
    this.getData();
    this.getCost();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@mixin main-font {
  font-family: "Sen", sans-serif;
}
@mixin secondary-font {
  font-family: "Roboto", sans-serif;
}

.error {
  color: red;

}

.summary-wrapper {
  height: auto;
  background-color: white;
  margin: 3%;
  border: 3px solid royalblue;

  p {
    @include main-font;
    color: royalblue;
    text-align: center;
    margin: 1%;
  }
  .order-head {
    font-size: 20px;
  }
  .orders-flex {
    width: 100%;
    display: flex;
    padding: 2% 3%;
  }
  .orders-charity {
    width: 50%;
    font-size: medium;
    padding: 0 3%;
    text-align: center;
  }
  .orders-cost {
    width: 25%;
    text-align: center;
  }
  .orders-quantity {
    width: 25%;
    text-align: center;
  }
  .headings {
    color: black;
    padding: 0;
    font-size: 17px;
    @include secondary-font;
    font-weight: 500;
  }
  .total {
    width: 100%;
    @include main-font;
    padding: 2% 5%;
  }
}
.payment-wrapper {
  height: auto;
  background-color: white;
  margin: 3%;
  border: 3px solid royalblue;

  .payment-head {
    @include main-font;
    color: royalblue;
    padding: 2% 0 0 0;
    font-size: 20px;
  }
  p {
    @include main-font;
  }
  .icon-head {
    margin-bottom: 1%;
  }
  .icon-wrapper {
    width: 60%;
    display: flex;
    margin: 0 auto;
    justify-content: center;

    img {
      height: 30px;
      width: 45px;
      padding: 1%;
    }
  }
  .card-form {
    display: flex;
    flex-direction: column;
    padding: 2%;
  }
  label {
    @include main-font;
    margin-bottom: 1%;
    padding-top: 10px;
  }
  input {
    height: 25px;
  }
  button {
    @include main-font;
    width: 75vw;
    display: flex;
    margin: 4% auto;
    padding: 2.5% 0;
    justify-content: center;
    height: 7vh;
    background-color: white;
    color: royalblue;
    border: 2px solid royalblue;
  }

  .split-fields {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .half {
    width: 48%;

    input {
      width: 100%;
    }
  }
}
@media only screen and (min-width: 1025px) {
  p, .summary-wrapper .headings {
    font-size: 1.4rem;
  }
  .summary-wrapper .headings {
    font-variant: all-small-caps;
    font-size: 1.5rem;
  }
  .flex-parent {
    display: flex;
    width: 100%;
    flex-wrap: nowrap;
    flex-direction: row;
  }
  .summary-wrapper .orders-flex {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    .orders-charity, .orders-cost, .orders-quantity {
      width: 100%;
      padding: 2.5% 0;
    }
  }
  .payment-wrapper, .summary-wrapper {
    width: 45%;

    button {
      max-width: 320px;
    }
    .icon-wrapper img {
      width: 60px;
      height: 40px;
    }
  }
  .payment-wrapper .payment-head {
    padding: 1% 0 0 0;
  }
}
</style>
