<template>
  <div class="purchase-wrap">
    <div class="box">
      <div class="image">
        <img src="../assets/mystery-icon.png" v-on:click="rollPrize" />
      </div>
      <div class="selection-text">
        <p>
          You have selected the: <span>{{ preference }}</span> Prize Pool
        </p>
        <button class="prizeButton" v-on:click="modalState = !modalState">
          View Potential Prizes
        </button>
        <div v-if="modalState" class="prizeModal">
          <div class="prize-list">
            <ul class="selection-grid">
              <h4 class="heading">Your potential prizes:</h4>
              <li
                v-for="preference in preferencePool"
                :key="preference"
                class="item"
              >
                {{ preference }}
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="pricing-box">
        <p>Select Quantity:</p>
        <div class="pricing-list">
          <button class="pricing-item">
            <p id="a" v-on:click="clicked">1 Roll: £10</p>
          </button>
          <button class="pricing-item b-parent">
            <p id="b" v-on:click="clicked">2 Rolls: £15</p>
          </button>
        </div>
        <div class="charities">
          <p>Select Charity:</p>
          <select v-model="chosenCharity">
            <option v-for="(charity, index) in charities" :key="index">{{
              charity.name
            }}</option>
          </select>
        </div>
        <div class="purchase-area">
          <button class="cont-button" v-on:click="nextPage">
            Continue
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Purchase",
  props: {},
  data() {
    return {
      preference: "",
      prizePool: [
        { name: "Nike Football", category: "Sports" },
        { name: "Tiger Plush", category: "Animals" },
        { name: "£10 Game Voucher", category: "Gaming" },
        { name: "Paw Patrol Toy Set", category: "Childrens" },
        { name: "Dad Jokes Book", category: "Novelty" },
        { name: "Personalised Coffee Mug", category: "Random" },
        { name: "Beginners Tennis Set", category: "Sports" },
        { name: "Cute Dog Print A4", category: "Animals" },
        { name: "Playstation Controller Skin", category: "Gaming" },
        { name: "David Walliams - Demon Dentist Book", category: "Childrens" },
        { name: "Whoopie Cushion", category: "Novelty" },
        { name: "Dove Bath Set", category: "Random" },
        { name: "Lego Characters X5", category: "Childrens" },
        { name: "Water Pistol", category: "Novelty" },
        { name: "Cadbury's Milk Box", category: "Random" },
        { name: "Learn To Draw: Animal Edition", category: "Animals"},
        { name: "Rugby Ball", category: "Sports" },
        { name: "Controller Charging Dock", category: "Gaming" },
      ],
      preferencePool: [],
      randomPool: [],
      prize: "",
      charities: [
        { name: "Featured Charity: Guide Dogs UK(+10% Donation)" },
        { name: "Bullying UK" },
        { name: "Macmillan Cancer Support" },
        { name: "St John Ambulance" }
      ],
      chosenCharity: "",
      quantity: "",
      modalState: false
    };
  },
  methods: {
    getPreference: function() {
      let p = localStorage.getItem("preference");
      this.preference = p;
    },
    getPreferencePool: function() {
      for (let i = 0; i < this.prizePool.length; i++) {
        //for each object in the prize pool
        if (this.prizePool[i].category === this.preference) {
          //if the category matches the users preference
          let item = this.prizePool[i].name.toString(); //take the name of the prize, parse it
          this.preferencePool.push(item); //push the prize into the new array
          this.randomPool.push(item); //push the prize into the random prize pool
        } else {
          let item = this.prizePool[i].name.toString(); //take the prize name, parse it
          this.randomPool.push(item); //push the item into the random prize pool
        }
      }
      return this.preferencePool;
    },
    rollPrize: function() {
      let specificPool = this.preferencePool;
      let prizePool = this.randomPool;
      for(let i = 0; i < this.quantity; i++){
        if (this.preference !== "Random") {
          //if user has chosen a preference roll on a specific prize pool
          let roll = specificPool[Math.floor(Math.random() * specificPool.length)];
          this.prize = roll;
          console.log("Your prize is: " + this.prize);
        } else {
          //otherwise roll on the entire table
          let roll = prizePool[Math.floor(Math.random() * prizePool.length)];
          this.prize = roll;
          console.log("Your prize is: " + this.prize);
        }
      }
    },
    storeChoices: function() {
      let charity = this.chosenCharity;
      localStorage.setItem("charity", charity);
      let quantity = this.quantity;
      localStorage.setItem("quantity", quantity);

      if(this.quantity === "1"){
        this.rollPrize();
        let prize = this.prize;
        localStorage.setItem("prize1", prize);
      }
      if (this.quantity === "2"){
        this.rollPrize();
        let prize1 = this.prize;
        localStorage.setItem("prize1", prize1);
        this.rollPrize();
        let prize2 = this.prize;
        localStorage.setItem("prize2", prize2);
      }

      this.$router.push("Summary");
    },
    clicked: function(event) {
      const e = event.target;
      if (e.id === "a") {
        e.classList.add("clicked");
        this.quantity = "1";
      } else if (e.id === "b") {
        e.classList.add("clicked");
        this.quantity = "2";
      }
    },
    nextPage: function(){
      this.storeChoices();
    },
  },
  created() {
    this.getPreference();
    this.getPreferencePool();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import url("https://fonts.googleapis.com/css2?family=Sen&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

@mixin main-font {
  font-family: "Sen", sans-serif;
}
@mixin secondary-font {
  font-family: "Roboto", sans-serif;
}

.clicked {
  color: white;
}
.purchase-wrap {
  width: 100vw;

  .box {
    padding: 5% 0;
    height: 40vh;

    .image {
      height: 35vh;
      width: 55%;
      margin: 0 auto;

      img {
        width: 100%;
      }
    }
    .selection-text {
      width: 100vw;
      @include main-font;
      p {
        text-align: center;
        color: white;
        margin-bottom: 1%;
        font-variant: all-small-caps;
        font-size: 20px;
        padding: 0 4%;
      }
      span {
        color: royalblue;
        font-variant: all-small-caps;
        font-size: 22px;
      }
      .prizeButton {
        background-color: royalblue;
        border-style: none;
        font-variant: all-small-caps;
        color: white;
        display: flex;
        margin: 15px auto 0;
        font-size: 18px;
        padding: 6px 16px 6px 16px;
      }
    }

    .pricing-box > p {
      color: white;
      font-variant: all-small-caps;
      @include main-font;
      font-size: 22px;
      text-align: center;
      padding-top: 5%;
    }
    .pricing-list {
      width: 100%;
      display: flex;
      padding: 0 10%;
      justify-content: space-between;

      .pricing-item {
        @include main-font;
        padding: 4px 8px;
        width: 35vw;
        list-style: none;
        text-align: center;
        background-color: white;
        border: 2px solid transparent;
        &:active,
        &:hover,
        &:visited {
          border: 2px solid royalblue;
          outline-color: royalblue;
        }
        p {
          margin: 4% 0;
          color: royalblue;
        }
      }
    }
    .charities {
      p {
        color: white;
        font-variant: all-small-caps;
        @include main-font;
        font-size: 22px;
        text-align: center;
      }
      select,
      option {
        max-width: 80%;
        display: flex;
        margin: 0 auto;
        @include main-font;
        font-size: 13px;
        color: royalblue;
        &:focus {
          outline-color: royalblue;
        }
      }
    }
    .cont-button {
      @include main-font;
      list-style: none;
      width: 80%;
      display: flex;
      margin: 4% auto;
      padding: 2.5% 0;
      justify-content: center;
      height: 7vh;
      background-color: aliceblue;
      color: royalblue;
      border: 2px solid royalblue;
    }
  }
  .prizeModal {
    width: 100%;
    display: flex;
    justify-content: center;
    .selection-grid {
      padding: 0;
    }
    .prize-list {
      display: flex;
      padding: 2% 2% 0 2%;
      flex-wrap: nowrap;
      justify-content: center;
      align-items: center;
      flex-direction: column;

    }
    .item {
      @include main-font;
      list-style: none;
      width: 100%;
      padding: 2% 0;
      margin: 0 0 3% 0;
      text-align: center;
      color: royalblue;
      background-color: white;
    }
  }
  .heading{
    @include main-font;
    padding-top: 3%;
    color: white;
    font-variant: all-small-caps;
    text-align: center;
  }
}
@media only screen and (min-width: 768px) {
  p, .purchase-wrap .box .selection-text p, .purchase-wrap .box .pricing-box > p, .purchase-wrap .box .charities p, .purchase-wrap .box .selection-text span {
    font-size: 1.5rem;
  }
  .image {
    max-width: 350px;
  }
}
.purchase-wrap .box .charities select {
  height: 25px;
}
@media only screen and (min-width: 992px) {
  p, .purchase-wrap .box .selection-text p, .purchase-wrap .box .pricing-box > p, .purchase-wrap .box .charities p, .purchase-wrap .box .selection-text span {
    font-size: 1.7rem;
  }
  .purchase-wrap .box .image {
    max-width: 250px;
    height: 28vh;
  }
  .purchase-wrap .box {
    padding: 2% 0;
    .pricing-box > p {
      padding-top: 25px;
    }

    .pricing-list {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .pricing-item {
        &:focus{
          border: 2px solid royalblue;
          outline-color: royalblue;
        }
        &:hover{
          border: none;
        }
        p {
          margin: 0;
        }
      }
    }
  }
  .purchase-wrap .box .charities select {
    height: 30px;
  }
  .purchase-wrap .box .cont-button {
    padding: 0;
    max-width: 332px;
  }
}

</style>
