<template>
  <div class="order-form">
    <h3 class="text-center heading main">Order Confirmed!</h3>
    <p class="text-right heading order">Order No: {{ orderReference }}</p>
    <p class="content">
      Thank you for your order, <span>{{ name }}</span
      >. Please find more details in the email sent to you at:
      <span>{{ email }}</span>
    </p>
    <h3 class="heading text-center">
      Your Prizes:
    </h3>
    <div class="prize-grid">
      <div class="item">
        {{ prize1 }}
      </div>
      <div v-if="prize2" class="item">
        {{ prize2 }}
      </div>
    </div>
      <p class="content bottom">Your donation to <span>{{charity}}</span> has been confirmed.</p>
      <p class="content bottom">
        For information on redeeming your prize/s, please refer to your order email.
      </p>
  </div>
</template>

<script>
export default {
  name: "Receipt",
  props: {},
  data() {
    return {
      name: "",
      email: "",
      orderReference: "",
      charity: "",
      prize1: "",
      prize2: ""
    };
  },
  methods: {
    getOrderDetails: function() {
      let n = localStorage.getItem("username");
      let e = localStorage.getItem("email");
      let p1 = localStorage.getItem("prize1");
      let p2 = localStorage.getItem("prize2");
      let c = localStorage.getItem("charity");


      this.name = n;
      this.email = e;
      this.prize1 = p1;
      this.prize2 = p2;
      this.charity = c;
    },
    generateOrderRef: function() {
      let check = localStorage.getItem("reference");
      if (check === null) {
        let ref = Math.floor(1000 + Math.random() * 9000);
        this.orderReference = ref;
        localStorage.setItem("reference", ref);
      } else {
        this.orderReference = check;
      }
    }
  },

  created() {
    this.generateOrderRef();
    this.getOrderDetails();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@mixin main-font {
  font-family: "Sen", sans-serif;
}

.order-form {
  background-color: white;
  margin: 3%;
  border: 2px solid royalblue;

  .heading {
    @include main-font;
    font-variant: all-small-caps;
    color: royalblue;
  }
  .order {
    padding: 0 3%;
  }
  .content {
    @include main-font;
    color: black;
    padding: 0 7%;
    text-align: center;
  }
  span {
    @include main-font;
    color: royalblue;
  }
  .prize-grid {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;

    .item {
      width: 60%;
      border: 2px solid royalblue;
      padding: 6px 14px;
      margin: 2%;
        @include main-font;
        text-align: center;
    }
  }
    .bottom {
        padding: 2% 7% 0 7%;
    }
}
@media only screen and (min-width: 768px) {
    p {
        font-size: 1.4rem;
    }
    .heading {
        font-size: 2rem;
    }
    .order-form .content {
        padding: 2%;
    }
    .order-form .prize-grid .item {
        font-size: 1.3rem;
    }
    .main {
        font-size: 2.5rem;
    }
}
@media only screen and (min-width: 1200px) {
    p {
        font-size: 1.5rem;
    }
    .heading {
        font-size: 2.2rem;
    }
    .order-form .content {
        padding: 20px;

    }
    .order-form .prize-grid .item {
        font-size: 1.5rem;
        margin: 1%;

    }
    .main {
        font-size: 2.7rem;
    }
}
</style>
