<template>
  <div class="container">
    <div class="form-wrap">
      <form>
        <ul class="login-form">
          <li class="username">
            <label>Username:</label>
            <input type="text" v-model="username" />
          </li>
          <li class="email-address">
            <label>Email:</label>
            <input type="text" v-model="email" />
          </li>
          <li class="password">
            <label>Password:</label>
            <input type="password" v-model="password" />
          </li>
        </ul>
      </form>
    </div>
    <div class="submit">
      <router-link to="/login"><button type="submit">Back</button></router-link>
      <button type="submit" v-on:click="register">Register</button>
    </div>
  </div>
</template>

<script>

  export default {
  name: "Login",
  props: {
  },
  data() {
    return {
      username: "",
      email: "",
      password: "",
    };
  },
  methods: {
    register: function() {
      let u = this.username;
      let e = this.email;
      let p = this.password;
      localStorage.setItem("username", u);
      localStorage.setItem("email", e);
      localStorage.setItem("password", p);
      alert(
        "Thank you for registering to Gift of Giving, " + this.username + "."
      );
      this.$router.push("/community");
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@mixin main-font {
  font-family: "Sen", sans-serif;
}
@mixin secondary-font {
  font-family: "Roboto", sans-serif;
}

a {
  color: black;

  &:hover {
    color: black;
    text-decoration: none;
  }
}

ul {
  list-style-type: none;
  padding-inline-start: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  li {
    label {
      @include main-font;
      width: 100%;
    }
    input {
      width: 100%;
      border: 1px #308fdb solid;
    }
  }
}

.form-wrap {
  background-color: white;
  margin: 20% 1% 10% 1%;
  display: flex;
  justify-content: center;
  align-content: center;
  height: 50vh;
  border-radius: 40px;

  form {
    display: flex;
    justify-items: center;
    justify-content: center;
    align-items: center;
  }
}

.login-form {
  padding: 0;

  li {
    display: inline-block;
  }
}

.login-wrap > form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.username,
.password,
.email-address{
  width: 256px;
  padding-top: 2%;
  padding-bottom: 2%;
}

.submit {
  display: flex;
  padding: 2%;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  button {
    @include main-font;
    height: 7vh;
    margin: 2% 0;
    background-color: white;
    border: none;
    width: 256px;
    text-decoration: none;
  }
}
@media only screen and (min-width: 768px) {
  .form-wrap {
    max-width: 700px;
    .login-form {
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;


      .username, .password, .email-address {
        width: 100%;
        padding: 7% 0;
      }
    }
  }
}
@media only screen and (min-width: 992px) {
  .form-wrap {
    margin: 5% auto;
  }
}
</style>